import React from "react";
import "../css/signup.css";
import { useForm } from "react-hook-form";
import Swal from 'sweetalert2';

function Signup() {
  const {
    handleSubmit,
    register,
    formState: { errors },
    getValues
  } = useForm('onChange');

  const onsubmit = (data)=> {
    console.log(data);
    const Toast = Swal.mixin({
      toast: true,
      position: "top-end",
      showConfirmButton: false,
      timer: 3000,
      timerProgressBar: true,
      didOpen: (toast) => {
        toast.onmouseenter = Swal.stopTimer;
        toast.onmouseleave = Swal.resumeTimer;
      }
    });
    Toast.fire({
      icon: "success",
      title: "Login in successfully"
    });

  }


  const submit = (data) => {
    console.log(data);
    fetch('https://jsonplaceholder.typicode.com/posts',{
      method: 'POST',
      body: JSON.stringify(data),
      headers: {
        'Content-Type':'application/json'
      }
    }).then(data => data.json())
    .then(res => {
      console.log(res)
      Swal.fire({
        title: 'Error!',
        text: JSON.stringify(res),
        icon: 'error',
        confirmButtonText: 'Cool'
      })
    })
    
   
  };
  return (
    <div id="Signup">
      <div className="container">
        <form onSubmit={handleSubmit(submit)} className="signupform">
          <div className="form-group">
            <div className="head">
              <h1>Signup</h1>
            </div>

            <div className="input-box">
              <input
                type="text"
                {...register("username", {
                  required: {
                    value: true,
                    message: "username is required",
                  },
                  minLength: {
                    value: 3,
                    message: "username should have atleast 3 characters",
                  },
                })}
                placeholder="Enter username &#xf007;"
                name="username"
                // style={{ fontFamily: "Arial, FontAwesome" }}
              />
              <small>{errors.username?.message}</small>
            </div>

            <div className="password-box">
              <input
                type="password"
                {...register("password", {
                  required: {
                    value: true,
                    message: "password is required",
                  },
                  // minLength: {
                  //     value: 8,
                  //     message: 'password should have atleast 8 characters'
                  // },
                  pattern: {
                    value:
                      /^(?=.[A-Za-z])(?=.\d)(?=.[@$!%#?&])[A-Za-z\d@$!%*#?&]{8,}$/,
                    message:
                      "password should have atleast one number,one character,one symbol",
                  },
                })}
                placeholder="Enter password  &#xf023;"
                name="password"
                style={{ fontFamily: "Arial, FontAwesome" }}

              />
              <small>{errors.password?.message}</small>
            </div>

            <div className="re-enter">
              <input
                type="password"
                {...register("reEnterPassword", {
                  required: {
                    value: true,
                    message: "re-enter password required",
                  },
                  validate: (value)=>{
                    console.log(value);
                    console.log(getValues('password'))
                    // alert(value)
                    return value === getValues('password')?true:'password and retype password not match'
                  }
                })}
                placeholder="Re-enter password &#xf023;"
                name="reEnterPassword"
                style={{ fontFamily: "Arial, FontAwesome" }}
              />
              <small>{errors.reEnterPassword?.message}</small>
            </div>

            <div className="signin btn">
              <button>Signup</button>
            </div>
          </div>
        </form>
      </div>
    </div>
  );
}

export default Signup;