function Pagination() {
  const [data, setdata] = React.useState([]);
  React.useEffect(() => {
    // alert("dummy");
    fetch("https://jsonplaceholder.typicode.com/comments")
      .then((data) => data.json())
      .then((jsondata) => {
        console.log(jsondata);
        setdata(jsondata)
      });
      // myPromise.then(
      //   function(value) {myDisplayer(value[1]+' success');},
      //   function(error) {myDisplayer(error+' Failure');}
      // );
  }, []);
  return (
    <div id="pagination">
      <div className="container">
        <h1>PAGINATION</h1>
        {data.length > 0 ? <Table data={data}></Table> : <p>loading...</p>}
      </div>
    </div>
  );
}
export default Pagination;