import React from 'react';
import profile from "../assets/emp.jpg"; 
import "../css/employeelist.css";

const Card = (Props) => {
    return (
        <div className="card">
            <img src={profile} alt="images"/>
            <div className="table">
                <p>Username :{Props.emp.Name}</p>
                <p>Id No :{Props.emp.EmployeeID}</p>
            </div>
            <button onClick={() => Props.btnfn()} id="view-btn">{Props.btntext}</button>
        </div>
    )
}
export default Card;