import React from "react";
import "../css/signup.css"
import "../css/login.css"
import {useForm} from "react-hook-form";
import Swal from 'sweetalert2';


const Login = () => {
  const {register,handleSubmit,formState:{errors}} =useForm();

  const onsubmit = (data)=> {
    console.log(data);
    const Toast = Swal.mixin({
      toast: true,
      position: "top-end",
      showConfirmButton: false,
      timer: 3000,
      timerProgressBar: true,
      didOpen: (toast) => {
        toast.onmouseenter = Swal.stopTimer;
        toast.onmouseleave = Swal.resumeTimer;
      }
    });
    Toast.fire({
      icon: "success",
      title: "Login in successfully"
    });

  }
    return(
  <div id="Login">
    <form onSubmit={handleSubmit(onsubmit)}>
    
    <div className="container">
      <label><i className="fa-regular fa-user"></i> USERNAME</label>
      <input type="text" {...register("username",{
        required:{
          value:true,
          message:'username is required'
        },
        minLength: {
         value:3,
         message: 'username should have atleast three characters'
        },
        maxLength: {
          value:10,
          message: 'username should have atleast ten characters'
        }
      })} />
      <small>{errors.username?.message}</small>
    </div>
    <div className="container">
      <label><i className="fa-solid fa-key"></i> PASSWORD</label>
      <input type="password" {...register('password')} />
    </div>
    <div className="container">
        <button><i className="fa-solid fa-right-to-bracket"></i> Login</button>
    </div>
    </form>
  </div>
)
};
export default Login;