import React from "react"
import "../css/navbar.css"
import { Link } from "react-router-dom";

const Navbar = ()=>{
    return(
        <nav>
            <u1>
                <li><Link to="/Home"><i class="fa-solid fa-house-signal"></i> home</Link></li>
                <li><Link to="/Login"><i class="fa-solid fa-user-secret"></i> login</Link></li>
                <li><Link to="/Signup"><i class="fa-solid fa-arrow-right-to-bracket"></i> Signup</Link></li>
                <li><Link to="/Calculator"><i class="fa-solid fa-calculator"></i> Calculator</Link></li>
                <li><Link to="/Employeelist"><i class="fa-regular fa-circle-user"></i> Employeelist</Link></li>
                <li><Link to="/pagination"><i class="fa-solid fa-clipboard"></i> pagination</Link></li>
                {/* <li><Link to="/Dummy"><i class="fa-regular fa-file"></i> Dummy</Link></li> */}
            </u1>
        </nav>
    )
}

export default Navbar;